import axios from '@/utils/uniAxios.js'
import {formatDate} from '@/utils/date.js'
export default {
	data() {
		return {
			form: {
				collect: [{
					time: '2019-04-06 11:37',
					shopName: '麦田圈官网旗舰店',
					state: 5,
				}],
			},
			order: {
				status: '',
				img: '',
				wuliu: '',
				pay: '',
				statusMsg: '',
				color: ''
			},
			statuss: '',
			show: false,
			marginBottom: '',
			checked: false,
			orderList: {
				name: '',
				phoneNumber: '',
				address: ''
			},
			ids: [],
			orderItemList: [],
			totalCount: '',
			payCode: {
				appId: "",
				nonceStr: "",
				package: "",
				paySign: "",
				signType: "MD5",
				timeStamp: ""
			},
			weekDay: ["周天", "周一", "周二", "周三", "周四", "周五", "周六"],
			promotionAmount:'',
			message:'',
			addressId:''
		}
	},
	onLoad(option) {
		console.log(option,7777)
		this.ids = JSON.parse(option.deleIds)
		this.addressId = option.addressId
		if (this.addressId) { //从地址跳转回来
			let orderAddress = JSON.parse(uni.getStorageSync('addressMsg'));
			// 地址更新
			this.orderList = {
				name: orderAddress.name,
				phoneNumber: orderAddress.phoneNumber,
				address: orderAddress.province + orderAddress.city + orderAddress.region + orderAddress.detailAddress,
			}
			axios.post('/order/getDetailByOrderId',{id:this.ids[0]}).then(res=>{
				console.log(res)
				this.orderList = res.data.data;
				this.orderItemList = res.data.data.orderItemList;
				this.totalCount = this.orderList.totalAmount - this.orderList.promotionAmount;
				this.promotionAmount = this.orderList.promotionInfo
			})
		}else{
			this.getOrder();
		}
	},
	onShow() {
		
		// this.getOrder();
	},
	filters: {
		formatDate(time) {
			var date = new Date(time);
			return formatDate(date, 'MM月dd日');
		}
	},
	methods: {
		noteBlur(event){
			console.log(event,this.orderList.note)
			this.orderList.note = event.detail.value;
			if(event.detail.value){
				let obj = {
					note:event.detail.value,
					orderId:this.orderList.id
				}
				axios.post('/order/update/note',obj).then(res=>{
					if(res.data.code==200){
						console.log('备注成功')
					}
				})
			}
		},
		// 数据初始化
		getOrder() {
			let obj = {
				cartItemIds: this.ids
			}
			axios.post('/order/generateOrder', obj).then(res => {
				this.orderList = res.data.data.order;
				this.orderItemList = res.data.data.orderItemList;
				this.totalCount = this.orderList.totalAmount - this.orderList.promotionAmount;
				this.promotionAmount = this.orderList.promotionAmount
			});
		},
		// 修改地址
		toAddress() {
			uni.navigateTo({
				url: '../set/address?postOrder=1&id=' + this.orderList.id
			})
		},
		//使用积分
		swithChange({
			detail
		}) {
			if(this.orderList.totalAmount<=1){
				this.$api.msg('总价小于1，不能使用积分')
			}else{
				// 计算实付金额
				this.checked = detail;
				
				if (this.checked) { //true，表示抵扣
				this.totalCount = this.orderList.totalAmount - 1 - this.orderList.promotionAmount;
				this.promotionAmount = this.orderList.promotionAmount+1
					// if (this.orderList.promotionAmount) {
					// 	this.totalCount = this.orderList.totalAmount - 1 - this.orderList.promotionAmount
					// } else {
					// 	this.totalCount = this.orderList.totalAmount - 1
					// 	this.promotionAmount = this.orderList.promotionAmount+1
					// }
				
				} else {
					if (this.orderList.promotionAmount) {
						this.totalCount = this.orderList.totalAmount - this.orderList.promotionAmount
					} else {
						this.totalCount = this.orderList.totalAmount
					}
				}
			}
		},
		// 立即支付
		payBtn() {
			let code = uni.getStorageSync('code')
			let obj = {
				code: code, //code
				orderSn: this.orderList.orderSn, //订单编号orderSn
				payType: 3, //支付类型
				rechargeMoney: this.totalCount, //支付金额s
			}
			let _this = this;
			axios.post('/pay/payOrder', obj).then(res => {
				console.log(res)
				_this.payCode = {
					appId: res.data.data.appId,
					nonceStr: res.data.data.nonceStr,
					package: res.data.data.package,
					paySign: res.data.data.paySign,
					signType: "MD5",
					timeStamp: res.data.data.timeStamp,
				}
				uni.requestPayment({
					provider: 'wxpay',
					timeStamp: _this.payCode.timeStamp,
					nonceStr: _this.payCode.nonceStr,
					package: _this.payCode.package,
					signType: 'MD5',
					paySign: _this.payCode.paySign,
					success: function(res) {
						console.log('success:' + JSON.stringify(res));
						uni.reLaunch({
							url: 'paySuccess?totalCount=' + _this.totalCount + '&id=' + _this.orderList.id
						})
					},
					fail: function(err) {
						console.log('fail:' + JSON.stringify(err));
					}
				})
			})
		},
		toDate:function(fmt){
			var o = {
				"M+": fmt.getMonth() + 1, //月份
				"d+": fmt.getDate(), //日
				"h+": fmt.getHours(), //小时
				"E": fmt.getDay(), //周几
				"m+": fmt.getMinutes(), //分
				"s+": fmt.getSeconds(), //秒
				"q+": Math.floor((fmt.getMonth() + 3) / 3), //季度
				"S": fmt.getMilliseconds() //毫秒
			};
			if (/(y+)/.test(fmt)) {
				fmt = fmt.replace(RegExp.$1, (fmt.getFullYear() + "").substr(4 - RegExp.$1.length));
			}

			for (var k in o) {
				if (new RegExp("(" + k + ")").test(fmt)) {
					fmt = fmt.replace(RegExp.$1, RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length));
				}
			}
			return fmt;
		}
	}
}
